

.drop-downs__block-general {
    display: none;
    flex-direction: column;
    max-width: 320px;
    width: 100%;

    background-color: white;
    z-index: 800;

    padding-left: 0.6em;
    padding-right: 0.4em;
    padding-bottom: 1.5em;

    border: 1px solid $DarkShade-25;

    position: absolute;
    top: 66px;
  


}
.drop-downs__open {
    display: block;
}
.drop-downs__block-row {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin: 0.6em 0;

        span {
            display: flex;
            justify-content: center;
            align-items: center;
            color: $DarkShade-50;

            height: 30px;
            width: 30px;

            border: 1px solid $DarkShade-50;
            border-radius: 50%;

            user-select: none;

                &:hover{
                    cursor: pointer;
                }
                &:active{
                    color: $DarkShade-75;
                    border-color: $DarkShade-75;
                }
               
        }
}
.drop-downs__minus_no-active {

        opacity: 0.5;

}
.drop-downs__score {
    display: flex;
    align-items: center;
    justify-content: space-between;

    max-width: 100px;
    width: 100%;
    
}
.drop-downs__guest {
    position: relative;
}
.drop-downs__buttons-guest {
    width: 100%;

    position: relative;

    margin-top: 0.6em;

        
        
}
.drop-downs__minus_active {
    opacity: 1;
}
