@import 'fonts';
@import 'mixin';
@import 'null';
@import 'var';
 input {
    cursor: pointer;
}
*{
    box-sizing: border-box;
}
h1, h2, h3 {
    font-family: "MontserratBold";
    padding: 0;
    margin: 0;
    color: $DarkShade-100;
   
}
h1 {
    font-size: $fn-m;
}
    
h2 {
    font-size: $fn-s;
}
h3 {
    font-size: $fn-xxs;
}
body {
    box-sizing: border-box;
}
.error {
    text-transform: none !important;
    color: rgb(252, 54, 54) !important;
    font-family: "MontserratRegular";
}
.container {
    margin: 0 auto;
    max-width: 1170px;
    width: 100%;
}
@include media(large) {
    .error {
        font-size: 14px;
    }
}

